.StoreManagerTabHomePage {
  
  padding-top: 64px;
  padding-bottom: 64px;

  .container_box {
    margin-top: 24px;
  }

  //ì²«ë²ì§¸ ë¨ start
  .first_area_box {
    display: flex;
  }
  .new_order_box {
    width: 319px;
    height: 378px;
    padding: 32px 24px;
    border-radius: 20px;
    box-shadow: 6px 4px 30px 0 rgba(25, 25, 25, 0.1);
    background-color: #ffffff;

    position: relative;
  }

  .new_order_top_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .new_order_top_title_text {
    font-size: 18px;
    font-weight: bold;
  }

  .new_order_top_more_button {
    font-size: 12px;
    display: flex;
    align-items: center;
    color: #999999;
  }

  .first_second_box {
    margin-left: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .calculate_box {
    width: 185px;
    height: 313px;
    padding: 32px 24px;
    border-radius: 20px;
    box-shadow: 6px 4px 30px 0 rgba(25, 25, 25, 0.1);
    background-color: #ffffff;
  }

  .link_copy_button {
    padding: 16px;
    border-radius: 20px;
    box-shadow: 6px 4px 18px 0 rgba(25, 25, 25, 0.1);
    background-color: #ffffff;
    font-size: 12px;
    font-weight: bold;
  }

  .point_color {
    color: #00bfff;
  }

  .new_order_no_content_container {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .new_order_no_content_text {
    text-align: center;
    font-size: 14px;
    color: #7f7f7f;
  }
